<template>
  <div class="tastList">
    <div class="operationBtn">
      <a-button type="primary"
                @click="onAddTask">
        <a-icon type="plus-circle" /> 添加
      </a-button>
    </div>
    <a-table bordered
             :dataSource="taskList"
             :columns="columns"
             :pagination='paging'
             @change="pagingChange"
             :style="{height: '635px'}"
             rowKey='id'>
      <div slot="delete"
           slot-scope="text, record, index">
        <a-button type="danger"
                  size="small"
                  @click="onDelectTask(text, index)">
          删除
        </a-button>
      </div>
      <div slot="lead"
           slot-scope="text, record, index">
        <a-button type="primary"
                  size="small"
                  @click="onLeadTask(text, index)">
          配置
        </a-button>
      </div>
    </a-table>
    <a-modal title="添加爬虫任务"
             v-model="Addmodel"
             @ok="addPresent"
             :centered='true'
             okText="确认"
             cancelText="取消">
      <a-form>
        <a-form-item label="名称"
                     :label-col="{ span: 5 }"
                     :wrapper-col="{ span: 12 }">
          <a-input placeholder="请输入名称"
                   v-model="taskName" />
        </a-form-item>
      </a-form>
    </a-modal>
    <a-modal title="导入任务"
             width="80%"
             v-model="Leadmodel"
             :centered='true'
              footer= ''>
      <div class="leadBtn">
         <a-button type="primary"
                @click="onLeadList">
                <a-icon type="rise" /> 导出
         </a-button>
         <a-select defaultValue="0" @change="selectList">
          <a-select-option value="0">新闻</a-select-option>
          <a-select-option value="1">论坛</a-select-option>
         </a-select>
      </div>
      <a-table :columns="leadList"
               :rowSelection="{
                  selectedRowKeys: selectedRowKeys,
                  onChange: onSelectChange
                }"
               :pagination='Leadpage'
               rowKey="pid"
               @change="LeadPageChange"
               :dataSource="LeadData">
      </a-table>
    </a-modal>
    <a-modal title="配置信息"
             width="80%"
             okText="导入"
             @ok="onLeadBtn"
             okType="default"
             @cancel="submitBtn"
             cancelText="提交"
             v-model="LeadListModel"
             >
        <a-form>
          <a-form-item label="名称"
                        :label-col="{ span: 9 }"
                        :wrapper-col="{ span: 7 }">
            <a-input placeholder="请输入名称"
                      />
          </a-form-item>
        </a-form>
        <!-- <table class="lead-table">
          <thead class="lead-head">
            <tr>
              <th>网址</th>
              <th>网址名称</th>
            </tr>
          </thead>
          <tbody class="lead-body">
            <tr v-for="(item, index) in arr" :key="index">
              <td>
                {{item.address}}
              </td>
              <td>
                {{item.webSite}}
              </td>
            </tr>
          </tbody>
        </table> -->
        <DataTable :LeadData="dataList" :count="dataListCount"
                    @currentChange="getCurrent"
                    ref="childTable"
        ></DataTable>
    </a-modal>
  </div>
</template>

<script>
import DataTable from './dataTable.vue'
let columns = [
  {
    title: '爬虫名称',
    dataIndex: 'task_name',
    width: '30%',
    align: 'center',
    scopedSlots: { customRender: 'name' }
  }, {
    title: '状态',
    customRender: (text) => {
      return text.status === 0 ? '未检测' : '已检测'
    },
    align: 'center'
  }, {
    title: '时间',
    customRender: (text, row, index) => {
      return text.create_time
    },
    align: 'center'
  }, {
    title: '导入',
    align: 'center',
    scopedSlots: { customRender: 'lead' }
  }, {
    title: '操作',
    align: 'center',
    scopedSlots: { customRender: 'delete' }
  }
]
let leadList = [
  {
    title: '网址',
    dataIndex: 'address',
    width: '30%',
    align: 'center'
  },
  {
    title: '网络名称',
    dataIndex: 'webSite',
    width: '30%',
    align: 'center'
  },
  {
    title: '网站类型',
    dataIndex: 'sort',
    width: '30%',
    align: 'center'
  }
]
export default {
  name: 'reptileList',
  components: {
    DataTable
  },
  data () {
    return {
      taskList: [],
      LeadData: [],
      pagination: { total: 0 },
      Leadpaging: {
        total: 0
      },
      taskParams: { 'page': 1, 'limit': 10, 'status': 0, 'keyword': '' },
      leadPage: 1,
      Addmodel: false,
      Leadmodel: false,
      LeadListModel: false,
      taskName: '',
      LeadObj: {},
      columns,
      leadList,
      selectedRowKeys: [],
      selectedItem: [],
      newObj: {},
      dataList: [],
      dataListCount: 0,
      TaskPage: 1,
      selectVal: 0,
      id: 1
    }
  },
  mounted () {
    this.AllTaskList()
  },
  methods: {
    // 下拉选项
    selectList (e) {
      this.selectVal = e
      this.onLeadBtn()
    },
    // 页数改变
    pagingChange (page) {
      let present = page.current
      this.taskParams.page = present
      this.AllTaskList()
    },
    // 导出列表页数改变
    LeadPageChange (page) {
      let present = page.current
      this.leadPage = present
      this.onLeadBtn()
    },
    // 关联所有的选中项
    onSelectChange (selectedRowKeys, selectedItem) {
      this.selectedRowKeys = selectedRowKeys
      this.selectedItem = selectedItem
      for (var i = 0; i < selectedItem.length; i++) {
        // console.log(selectedItem[i].address)
        this.newObj[selectedItem[i].pid] = {address: selectedItem[i].address, webSite: selectedItem[i].webSite, key: selectedItem[i].pid + 'a', pid: selectedItem[i].pid}
      }
    },
    // 导出选中的任务列表
    onLeadList () {
      this.arr = []
      for (var j = 0; j < this.selectedRowKeys.length; j++) {
        this.arr.push(this.newObj[this.selectedRowKeys[j]])
      }
      console.log(this.arr)
      this.$refs.childTable.getLeadList(this.arr)
      this.LeadListModel = true
      this.Leadmodel = false
    },
    // 搜有爬虫任务列表
    AllTaskList () {
      let obj = this.taskParams
      let url = `${this.baseURL}/task/select?
                page=${obj.page}&limit=${obj.limit}&
                status=${obj.status}&keyword=${obj.keyword}`.replace(/\s/g, '')
      this.$http.get(url).then((res) => {
        console.log(res)
        this.pagination.total = res.data.count
        this.taskList = res.data.data
      })
    },
    // 添加爬虫任务
    onAddTask () {
      this.Addmodel = true
    },
    // 删除爬虫任务
    onDelectTask (text, index) {
      let url = `${this.baseURL}/task_config/select_by_id?id=${text.id}`
      this.$http.get(url).then((res) => {
        let len = res.data.data.length
        console.log(len)
        if (len > 0) {
          this.warning('配置中网址信息未全部移除,不能删除')
        } else {
          this.showConfirm('删除任务', '您确定要删除此任务', text.id)
        }
      })
    },
    // 确定提交
    addPresent () {
      let url = `${this.baseURL}/task/add?task_name=${this.taskName}`
      this.$http.get(url).then((res) => {
        if (res.status === 200) {
          this.success('添加成功')
          this.Addmodel = false
          this.AllTaskList()
        }
      }, (reject) => {
      })
    },
    // 导入任务配置
    onLeadTask (test) {
      this.LeadListModel = true
      this.id = test.id
      let url = `${this.baseURL}/task_config/select_by_id?id=${this.id}`
      this.$http.get(url).then((res) => {
        console.log(res)
        let arr = res.data.data
        let newArr = arr.map((item, index) => {
          item.key = index
          return item
        })
        this.dataList = newArr
        this.dataListCount = res.data.count
      })
    },
    // 获取子组件传过来的current
    getCurrent (cur) {
      this.TaskPage = cur
    },
    // 导入事件
    onLeadBtn () {
      this.Leadmodel = true
      let url = `${this.baseURL}/task_config/select_all?page=${this.leadPage}&limit=10&sort=${this.selectVal}`
      this.$http.get(url).then((res) => {
        console.log(res.data)
        this.LeadData = res.data.data
        // this.Leadpaging.total = res.data.count
      })
    },
    // 提交事件
    submitBtn () {
      let leave = []
      let removePidArr = this.$refs.childTable.removePidArr
      this.$refs.childTable.NewLeadData.map((item, index) => {
        leave.push(item.pid)
      })
      let url = `${this.baseURL}/task_config/spider_name?task_id=${this.id}&main_url_pids=[${leave}]&main_url_remove_pids=[${removePidArr}]`
      this.$http.get(url).then((res) => {
        console.log(res.data)
      })
    },
    // 确认对话框
    showConfirm (title, content, id) {
      let this_ = this
      this.$confirm({
        title: title,
        content: content,
        cancelText: '取消',
        okText: '确认',
        onOk () {
          let url = `${this_.baseURL}/task/delete?id=${id}`
          this_.$http.get(url).then((res) => {
            this_.success('删除成功')
            this_.AllTaskList()
          }, (reject) => {
            this_.error('删除失败')
          })
        },
        onCancel () { }
      })
    },
    // 成功提示
    success (text) {
      this.$message.success(text)
    },
    warning (text) {
      this.$message.config({
        top: '20%',
        duration: 2,
        maxCount: 3
      })
      this.$message.warning(text)
    }
  },
  watch: {
    // pagination: {
    //   handler () {
    //     console.log(this.pagination)
    //   },
    //   immediate: true,
    //   deep: true
    // }
  },
  computed: {
    paging: function () {
      return this.pagination
    },
    Leadpage: function () {
      return this.Leadpaging
    }
  }
}
</script>

<style lang="stylus" scoped>
.tastList {
  .operationBtn {
    padding: 20px 20px;
  }
}
// .leadBtn{
//     margin-bottom: 24px
//   }
