//   width: 100%
//   text-align: center
//   height: 100%
//   .lead-head{
//     display: table
//     width: 100%
//     height: 32px
//     line-height: 32px
//     table-layout: fixed
//   }
//   .lead-body::-webkit-scrollbar{
//     display: none
//   }
//   .lead-body{
//     display:inline-block
//     height: 300px
//     overflow-y: auto
//     tr{
//       display: table
//       width: 100%
//       table-layout: fixed
//     }
//   }
// }
</style>



