<template>
  <div class="data-table">
      <a-table :columns="leadList"
               :pagination='{total: Newcount}'
                rowKey="pid"
                @change="PageChange"
               :dataSource="NewLeadData">
        <div slot="delete"
           slot-scope="text, record, index">
          <a-button type="danger"
                    size="small"
                    @click="removeItem(text, index, record.key, record.pid)"
                    >
            移除
          </a-button>
        </div>
      </a-table>
  </div>
</template>

<script>
let leadList = [
  {
    title: '网址',
    dataIndex: 'address',
    width: '30%',
    align: 'center'
  },
  {
    title: '网络名称',
    dataIndex: 'webSite',
    width: '30%',
    align: 'center'
  },
  {
    title: '操作',
    scopedSlots: { customRender: 'delete' },
    width: '30%',
    align: 'center'
  }
]
export default {
  name: 'dataTable',
  props: {
    LeadData: {
      type: Array
    },
    count: {
      type: Number
    }
  },
  data () {
    return {
      pagination: {total: 0},
      Leadpage: {},
      Newcount: this.count,
      NewLeadData: this.LeadData,
      removePidArr: [],
      leadList
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    getLeadList (list) {
      this.NewLeadData = this.NewLeadData.concat(list)
      console.log(this.NewLeadData)
    },
    PageChange (page) {
      this.$emit('currentChange', page.current)
    },
    removeItem (text, index, key, pid) {
      this.removePidArr.push(pid)
      const dataSource = [...this.NewLeadData]
      //  通过过滤的方法进行移除
      this.NewLeadData = dataSource.filter(item => item.key !== key)
      this.Newcount = this.NewLeadData.length
    }
  },
  watch: {
    Leadmodel: function (oldValue, newValue) {
    },
    LeadData: function () {
      this.NewLeadData = this.LeadData
    }
  },
  computed: {
  }
}
</script>

<style lang="stylus" scoped>
.data-table >>> .ant-table-tbody > tr > td{
  padding: 8px 16px
}
.data-table{
  position: relative
  height: auto
}
</style>



